import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { getAllChefReviewSaga } from '../../../store/actions';
import { Table } from '../../../components';

const tableFields = [
  {
    displayName: 'Order ID',
    key: 'orderNumber',
  },
  {
    displayName: 'User Name',
    key: 'userName',
  },
  {
    displayName: 'Vendor Rating',
    key: 'rating',
    className: 'active-green',
    isRating: true,
    ratingType: 'star',
  },
  {
    displayName: 'Quality',
    key: 'taste',
    isRating: true,
  },
  // {
  //   displayName: 'Temperature',
  //   key: 'temperature',
  //   isRating: true,
  // },
  {
    displayName: 'Packaging',
    key: 'packaging',
    isRating: true,
  },
  {
    displayName: 'Price Value',
    key: 'priceValue',
    isRating: true,
  },
  {
    displayName: 'Review',
    key: 'review',
  },
];

// eslint-disable-next-line no-unused-vars
const Reviews = props => {
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const {
    reviews: { list: allData, totalRecords },
    errorMsg,
  } = useSelector(state => state.review);

  const dispatch = useDispatch();

  const getData = (columnName, orderBy, search, skip, limit) => {
    const { id } = props.match.params;
    if (search && skip !== 0) {
      dispatch(
        getAllChefReviewSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
          chefId: id,
        }),
      );
    } else {
      dispatch(
        getAllChefReviewSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
          chefId: id,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('orderNumber', 'desc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const linkObj = {};

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Reviews </h2>
              </div>

              <div className="coman-table" id="owner-detail-btn2">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  link={linkObj}
                  linkPropsName="_id"
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  initialSortColumnName="orderNumber"
                  initialSortOrderBy="desc"
                  // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SetTokenHeader(withRouter(Reviews), axios);
